___CSS_LOADER_EXPORT___.push([module.id, `.header {
  height: 3rem;
  width: 100%;
  box-shadow: 0 0.1rem 0.4rem rgba(0, 0, 0, 0.1);
  line-height: 2.5rem;
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  background-color: #fff;
  z-index: 100;
}
.header .logo {
  font-size: 1.8rem;
  font-family: ZCOOLXiaoWei;
  font-weight: bolder;
  color: #42b983;
  margin: 0 1rem;
  margin-right: 3rem;
}
.header ul {
  display: flex;
  flex-direction: row;
  font-size: 0.8rem;
  letter-spacing: 0.1rem;
  font-family: MarryCN;
}
.header ul li {
  margin-right: 2rem;
  cursor: pointer;
  position: relative;
}
.header ul li:nth-last-child(1) {
  margin-right: 0rem;
}
.header ul .active {
  color: #ea4a09;
}
.header .sub {
  position: absolute;
  z-index: 2;
  background: #26303a;
  color: #fff;
  margin: 0;
  padding: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
}
.header .sub li {
  margin-right: 0rem;
  padding: 0 2px;
  margin: 0;
}
